@import "../variables.scss";

.create-activity-form {
  position: fixed;
  bottom: 10px;
  left: 10px;
  background: $main-background-color;
  padding: 10px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  gap: 5px;

  & div {
    display: flex;
    gap: 2px;
  }

  & input[type="number"] {
    width: 30px;
  }
}
