@import "../variables.scss";

.activity {
  background-color: $block-background-color;
  grid-area: activity;
  padding: 5px;
}
.activity__tools {
  padding: 5px;
  display: flex;
}

.interval-controller {
  display: flex;
  gap: 5px;
  border-radius: 10px;
  padding: 5px;
  background: $main-background-color;
}
.interval-controller__button {
  cursor: pointer;
}

.activity-calendar {
  margin-top: 5px;
  width: 100%;
  border-collapse: collapse;
  table-layout: fixed;
  & td {
    border-left: 1px solid $main-background-color;
    border-top: 1px solid $main-background-color;
  }
  & td:last-child {
    border-right: 1px solid $main-background-color;
  }

  & tr:last-child {
    border-bottom: 1px solid $main-background-color;
  }
}
.vertical-legend {
  width: 30px;
  padding: 5px;
  vertical-align: middle;
  text-align: center;
}
.month-legend {
  text-transform: capitalize;
  background-color: $main-background-color;
  padding: 2px;
}
.day-legend {
  padding: 2px;
}

.row-border {
  background-color: rgba(0, 124, 255, 0.1);
}

.today-legend {
  & .month-legend {
    background-color: rgba(0, 123, 255, 0.575);
    color: white;
  }
}

.timeslot {
  padding: 2px;
  gap: 5px;
  display: flex;
  flex-direction: column;
}

.event {
  background: rgba(0, 123, 255, 0.575);
  padding: 2px;
  border-radius: 5px;
  text-transform: capitalize;
}
