@import "../variables.scss";

.main-info {
  background-color: $block-background-color;
  padding: 5px;
  grid-area: main;
}
.main-info__name {
  font-size: 16px;
  font-weight: 700;
  padding-left: 5px;
}
.main-info__table {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin: 0 -5px;
  margin-top: 10px;

  & div {
    padding: 7px 5px;
    border-bottom: 1px solid $main-background-color;
  }

  & div:nth-child(2n + 1) {
    color: $secondary-color;
  }
}
